.board-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info-panel {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  font-size: 20px;
}

.info-panel > div {
  margin: 0 10px;
}

.info-panel > div:not(:last-child)::after {
  content: '|';
  margin-left: 10px;
}

.board {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr)); /* Adjust card size */
  grid-gap: 10px; /* Adjust gap between cards */
  justify-content: center;
  align-items: center;
  padding: 5px;
  box-sizing: border-box;
}
